"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription } from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Calendar } from "@/components/ui/calendar";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { format } from "date-fns";
import { Clock, User } from "lucide-react";

interface ScheduleSessionModalProps {
  isOpen: boolean;
  onClose: () => void;
  role: "TUTOR" | "STUDENT";
  availableUsers: Array<{
    id: string;
    name: string;
    avatar?: string;
  }>;
}

export function ScheduleSessionModal({ isOpen, onClose, role, availableUsers }: ScheduleSessionModalProps) {
  const router = useRouter();
  const [date, setDate] = useState<Date | undefined>(new Date());
  const [time, setTime] = useState<string>("");
  const [selectedUser, setSelectedUser] = useState<string>("");
  const [duration, setDuration] = useState<string>("60");
  const [topic, setTopic] = useState<string>("");

  const handleSubmit = async () => {
    if (!date || !time || !selectedUser || !duration || !topic) return;

    // Create session request
    const sessionData = {
      date: format(date, "yyyy-MM-dd"),
      time,
      duration: parseInt(duration),
      topic,
      [role === "TUTOR" ? "studentId" : "tutorId"]: selectedUser,
      status: "pending",
    };

    try {
      // TODO: Replace with actual API call
      console.log("Creating session request:", sessionData);
      
      // Close modal and refresh the page
      onClose();
      router.refresh();
    } catch (error) {
      console.error("Error creating session:", error);
    }
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="sm:max-w-[425px]">
        <DialogHeader>
          <DialogTitle>Schedule New Session</DialogTitle>
          <DialogDescription>
            Select a date, time, and {role === "TUTOR" ? "student" : "tutor"} for your session.
          </DialogDescription>
        </DialogHeader>
        <div className="grid gap-4 py-4">
          <div className="grid gap-2">
            <Label htmlFor="date">Date</Label>
            <Calendar
              mode="single"
              selected={date}
              onSelect={setDate}
              disabled={(date) => date < new Date()}
              initialFocus
            />
          </div>
          <div className="grid gap-2">
            <Label htmlFor="time">Time</Label>
            <div className="flex items-center space-x-2">
              <Clock className="h-4 w-4 text-muted-foreground" />
              <Input
                id="time"
                type="time"
                value={time}
                onChange={(e) => setTime(e.target.value)}
                className="w-full"
              />
            </div>
          </div>
          <div className="grid gap-2">
            <Label htmlFor="duration">Duration (minutes)</Label>
            <Select value={duration} onValueChange={setDuration}>
              <SelectTrigger>
                <SelectValue placeholder="Select duration" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="30">30 minutes</SelectItem>
                <SelectItem value="60">1 hour</SelectItem>
                <SelectItem value="90">1.5 hours</SelectItem>
                <SelectItem value="120">2 hours</SelectItem>
              </SelectContent>
            </Select>
          </div>
          <div className="grid gap-2">
            <Label htmlFor="user">Select {role === "TUTOR" ? "Student" : "Tutor"}</Label>
            <Select value={selectedUser} onValueChange={setSelectedUser}>
              <SelectTrigger>
                <SelectValue placeholder={`Select ${role === "TUTOR" ? "student" : "tutor"}`} />
              </SelectTrigger>
              <SelectContent>
                {availableUsers.map((user) => (
                  <SelectItem key={user.id} value={user.id}>
                    <div className="flex items-center space-x-2">
                      <User className="h-4 w-4" />
                      <span>{user.name}</span>
                    </div>
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
          <div className="grid gap-2">
            <Label htmlFor="topic">Session Topic</Label>
            <Input
              id="topic"
              placeholder="What will you be working on?"
              value={topic}
              onChange={(e) => setTopic(e.target.value)}
            />
          </div>
        </div>
        <div className="flex justify-end space-x-2">
          <Button variant="outline" onClick={onClose}>
            Cancel
          </Button>
          <Button onClick={handleSubmit} disabled={!date || !time || !selectedUser || !duration || !topic}>
            Request Session
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  );
} 